<template>
	<section class="login">
		<div class="container">
			<div class="row flex-column justify-content-center align-items-center">
				<h3 class="section-title__text">Увійти до системи</h3>
				<form @submit.prevent='login'>
					<input class="form-control" v-model='email' required type="email" placeholder="Email">
					<input class="form-control" v-model='password' required type="password" placeholder="Пароль">
					<button class="admin-button" type="submit">Увійти</button>
				</form>
				
			</div>
			
		</div>
	</section>
</template>
<script>
	export default {
		name: 'login',
		data() {
			return {
				email: '',
				password: '',
			}
		},
		methods: {
			login() {
				this.$_auth.signInWithEmailAndPassword(this.email, this.password)
					.then(user => {
						this.$router.replace('admin');
						this.$store.commit('isLogIn');
					})
					.catch(err => alert(err));
				
			}
		},
	}
</script>
<style lang="sass" scoped>
	@import '~@/assets/sass/_vars'

	.login 
		margin-top: 40px
		margin-bottom: 40px
	
	input 
		margin: 10px 0
		padding: 20px 25px
		display: block
		color: $primary-text
		font-family: $ff
		font-size: 14px
	button
		display: block
		width: 100%
		cursor: pointer
		font-family: $ff
	
</style>